import React from "react";
import { StyleSheet, css } from "aphrodite";

import Button from "common/Button";

import CircularMapPreview from "checkout/CircularMapPreview";
import ManhattanMapPreview from "checkout/ManhattanMapPreview";

const Preview = props => {
  const { order, onValidPromoCodeEntered, promoCode } = props;
  const handlePromoCodeClicked = () => {
    const promoCode = prompt("Enter Map Lamps promotional code");
    // if you are reading this ... you win 10% off! =D
    if (promoCode === "map_lamp_champ") {
      onValidPromoCodeEntered(promoCode);
    } else {
      alert("Oops! We didn't recognize that as a valid promotional code.");
    }
  };
  return (
    <div className={css(styles.preview)}>
      <div className={css(styles.container)}>
        {order.model === "circular" ? (
          <CircularMapPreview order={order} />
        ) : (
          <ManhattanMapPreview order={order} />
        )}
        <p>
          {promoCode ? (
            <React.Fragment>
              Total = $450
              <span className={css(styles.percentOff)}>(10% off!)</span>
            </React.Fragment>
          ) : (
            "Total = $500"
          )}
        </p>
        {props.completedOrderId ? null : (
          <Button onClick={handlePromoCodeClicked} disabled={promoCode}>
            Enter promo code
          </Button>
        )}
      </div>
    </div>
  );
};

const ConfigItem = ({ name, value }) => {
  return (
    <tr className={css(styles.item)}>
      <td className={css(styles.itemName)}>{name}</td>
      <td>{value}</td>
    </tr>
  );
};

export default Preview;

const styles = StyleSheet.create({
  percentOff: {
    marginLeft: "5px",
    color: "#228B22"
  },
  preview: {
    boxSizing: "border-box",
    width: "300px",
    marginLeft: "30px",
    background: "white",
    position: "sticky",
    top: "30px",
    padding: "20px",
    alignSelf: "start",
    "@media (max-width: 820px)": {
      marginBottom: "30px",
      marginLeft: "0px",
      position: "static",
      alignSelf: "center",
      width: "100%"
    }
  },
  container: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center"
  },
  thumbnail: {
    width: "200px"
  },
  configTable: {
    tableLayout: "fixed",
    marginTop: "20px",
    marginBottom: "20px",
    width: "100%"
  },
  item: {},
  itemName: {
    color: "gray",
    textAlign: "right"
  }
});
